import common from '@/utils/common.js';
import Bmob from "hydrogen-js-sdk";

import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import loading from "@/components/Loading/index.vue"
import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue'
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'

let that;
let uid;
let shopId;
let index;

export default {
	components: {
		faIcon,
		loading,
		uniNavBar,
		uniSegmentedControl
	},
	data() {
		return {
			showOptions: false,
			header: {
				total: 0,
				total_money: 0,
				get_money: 0,
			}, //头部的统计数据
			loading: true,
			items: ['今天', '昨天', '近七天', '近一个月'],
			list: [],
			current: 0,

			now_day: common.getDay(0, false),
			end_day: common.getDay(1, false),
			max_day: common.getDay(0, false),

			seleted_tab: -1, //1采购  -1销售  2退货
			selected_text: "销售",
			types: [{
				name: '采购',
				type: 1
			}, {
				name: '销售',
				type: -1
			}, {
				name: '退货',
				type: 2
			}, ]
		}
	},
	onLoad(options) {
		that = this;
		uid = wx.getStorageSync("uid");
		shopId = options.shopId
		//shopId = "ff3ae14ee9"
		that.getdetail();
	},
	methods: {

		//选择类型改变
		bindtypechange(e) {
			console.log(e)
			index = e.detail.value
			that.selected_text = that.types[index].name
		},

		option_confrim() {
			that.header = {
				total: 0,
				total_money: 0,
				get_money: 0,
			}
			that.seleted_tab = that.types[index].type

			that.showOptions = false;
			that.getdetail()
		},

		//modal重置的确认点击
		option_reset() {
			that.header = {
				total: 0,
				total_money: 0,
				get_money: 0,
			}
			that.seleted_tab = -1;
			that.selected_text = '销售';

			that.now_day = common.getDay(0, false),
				that.end_day = common.getDay(1, false),
				that.max_day = common.getDay(0, false),
				that.showOptions = false;
			that.getdetail()
		},

		bindDateChange1(e) {
			that.now_day = e.detail.value;
		},

		bindDateChange2(e) {
			that.end_day = e.detail.value;
		},

		//日期筛选
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index
				that.header = {
					total: 0,
					total_money: 0,
					get_money: 0,
				}
				if (index == 0) {
					that.now_day = common.getDay(0, false)
					that.end_day = common.getDay(1, false)
				} else if (index == 1) {
					that.now_day = common.getDay(-1, false)
					that.end_day = common.getDay(0, false)
				} else if (index == 2) {
					that.now_day = common.getDay(-7, false)
					that.end_day = common.getDay(1, false)
				} else if (index == 3) {
					that.now_day = common.getDay(-30, false)
					that.end_day = common.getDay(1, false)
				}

				that.getdetail()
			}
		},

		getdetail() {
			const query = Bmob.Query("Bills");
			query.equalTo("userId", "==", uid);
			query.equalTo("type", "==", that.seleted_tab);
			query.equalTo("shop", "==", shopId);
			query.equalTo("createdAt", ">=", that.now_day + ' 00:00:00');
			query.equalTo("createdAt", "<=", that.end_day + ' 00:00:00');
			query.order("-createdAt")
			query.include("operater", "goodsId");
			query.find().then(res => {
				console.log(res)
				for (let item of res) {
					console.log(item)
					that.header.total += item.num
					that.header.total_money += Number(item.goodsId.costPrice) * item.num
					that.header.get_money += item.total_money - Number(item.goodsId.costPrice) * item.num
				}
				that.list = res;
				that.loading = false;
			});
		}
	}
}
